(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('gridExpand', gridExpandFunc)
    .directive('gridExpandTrigger', gridExpandTriggerFunc);

  /** @ngInject */
  /**
   * 表格展开更多信息
   * @return {object}
   */
  function gridExpandFunc() {
    return {
      restrict: 'A',
      templateUrl: function(element, attrs) {
        return attrs.tplSrc || ''
      },
      link: function(scope, element) {
        element.addClass('tr-expand');
      }
    }
  }

  /** @ngInject */
  /**
   * 展开表格开关
   * @return {object} 指令对象
   */
  function gridExpandTriggerFunc() {
    return {
      restrict: 'A',
      template: '<i class="fa" ng-class="{\'fa-caret-right\': vm.expanedIndex !== $index, \'fa-caret-down\': vm.expanedIndex == $index}">',
      link: function(scope, element, attrs) {
        var td_parent = element.parent()[0];
        var method = attrs.gridExpandMethod;
        var ctrl = scope.$parent.vm;

        ctrl.expanded = false;
        element.addClass('td-expand print-remove');

        element.unbind().bind('click', function() {
          var tdLen = td_parent.querySelectorAll('td').length;

          if ((!ctrl.expanded || ctrl.expanedIndex !== scope.$index) && method && method !== '') {
            ctrl[method](scope.row[attrs.gridP]);
            ctrl.expanedIndex = scope.$index;
            ctrl.expanded = true;
          } else {
            ctrl.expanedIndex = null;
            ctrl.expanded = false;
          }

          scope.tdLen = tdLen;
        });
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('gridCheckbox', gridCheckboxFunc);

  /** @ngInject */
  /**
   * 表格多选框
   * @return {object}
   */
  function gridCheckboxFunc() {
    return {
      require: '^stTable',
      template: '<input type="checkbox"/>',
      scope: {
        row: '=gridCheckbox'
      },
      link: function(scope, element, attr, ctrl) {
        element.addClass('print-remove');

        element.bind('change', function() {
          scope.$apply(function() {
            ctrl.select(scope.row, 'multiple');
          });
        });

        scope.$watch('row.isSelected', function(newValue) {
          /*
          选择框与打印结合，勾选了某项后，打印不显示。
           */
          var printWindowElement = document.querySelector('#printWindow');
          var printWindow;
          if (printWindowElement) {
            printWindow = printWindowElement.contentWindow;
          }
          var rowClass = '.' + Array.prototype.find.call(element.parent()[0].classList, function(classItem) {
            return classItem.indexOf('row') >= 0;
          });
          var printWindowRow;

          if (rowClass !== '' && rowClass && printWindow) {
            printWindowRow = printWindow.document.querySelector(rowClass);
          }
          
          if (newValue === true) {
            element.parent().addClass('active print-remove');
            if (printWindowRow) {
              angular.element(printWindowRow).addClass('print-remove')
            }
          } else {
            element.parent().removeClass('active print-remove');
            if (printWindowRow) {
              angular.element(printWindowRow).removeClass('print-remove')
            }
          }

          element[0].querySelector('input').checked = typeof newValue !== 'undefined' || newValue ? newValue : false;
        });
      }
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('gridCheckAll', gridCheckAllFunc);

  /** @ngInject */
  /**
   * 表格全选
   * @return {object}
   */
  function gridCheckAllFunc() {
    return {
      restrict: 'E',
      template: '<input type="checkbox" ng-model="isAllSelected" />',
      scope: {
        all: '='
      },
      link: function (scope, element) {
        element.parent().addClass('checkbox-td print-remove');

        scope.$watch('isAllSelected', function () {
          if (typeof scope.all === 'undefined') {
            return;
          }
          scope.all.forEach(function (val) {
            val.isSelected = scope.isAllSelected;
          })
        });

        scope.$watch('all', function (newVal, oldVal) {
          if (oldVal) {
            oldVal.forEach(function (val) {
              val.isSelected = false;
            });
          }

          scope.isAllSelected = false;
        });
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('techFilesService', techFilesService);

  /** @ngInject */
  function techFilesService($window, $http, apiHost) {
    var reqs = apiHost.techFiles,
      service = {
        getNurseList: getNurseList,
        updateNurseInfo: updateNurseInfo,
        getNurseHirReg: Getter('nurseHirReg'),
        saveNurseHirReg: Setter('nurseHirReg'),
        deleteNurseHirReg: Deleter('nurseHirReg'),
        getNurseExp: Getter('nurseExp'),
        saveNurseExp: Setter('nurseExp'),
        deleteNurseExp: Deleter('nurseExp'),
        getAcademic: Getter('academic'),
        saveAcademic: Setter('academic'),
        deleteAcademic: Deleter('academic'),
        getAwardPunish: Getter('awardPunish'),
        saveAwardPunish: Setter('awardPunish'),
        deleteAwardPunish: Deleter('awardPunish'),
        getPerAssessment: Getter('perAssessment'),
        savePerAssessment: Setter('perAssessment'),
        deletePerAssessment: Deleter('perAssessment')
      };

    return service;

    function getNurseList(deptCode) {
      var req = reqs.getNurseList;
      return $http[req.method](req.url, {
        params: {
          deptCode: deptCode
        }
      });
    }

    function updateNurseInfo(data) {
      var req = reqs.updateNurseInfo;
      return $http[req.method](req.url, {
        comUser: data,
        dataType: 'comUser'
      });
    }

    function Getter(type) {
      return function(id) {
        var req = reqs[type].getter;
        return $http[req.method](req.url, {
          userCode: id
        });
      }
    }

    function Setter(type) {
      return function(data) {
        var req = reqs[type].setter;
        return $http[req.method](req.url, data);
      }
    }

    function Deleter(type) {
      return function(id) {
        var req = reqs[type].deleter;
        return $http[req.method](req.url, {
          params: {
            seqId: id
          }
        });
      }
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('TechFileController', techFileController);

  /** @ngInject */
  function techFileController($rootScope, $scope, $uibModal, deptService, NgTableParams, techFilesService) {
    var vm = this;

    vm.rootScope = $rootScope;
    vm.scope = $scope;

    //删除用户数据
    vm.del = del;
    //添加用户
    vm.open = open;
    // 加载用户列表
    vm.load = load;
    //加载用户详细信息
    vm.loadDetail = loadDetail;
    vm.detail = {};
    vm.genders = ['女', '男'];

    deptService.getDeptData().then(function(data) {
      vm.deptData = data;
      vm.getDeptName = _.memoize(function(wardCode) {
        var name;
        _.some(data.belongedDepts, function(item) {
          if (item.deptCode == wardCode) {
            name = item.deptName;
            return true;
          }
        });
        return name;
      });
    });


    function del(userCode) {
      techFilesService.delNurseJCI(userCode).then(function(response) {
        $rootScope.$broadcast('toast', {
          type: 'success',
          content: '删除成功'
        });
        load();
      });
    }

    function open(data) {
      var modalInstance = $uibModal.open({
        templateUrl: 'app/techFiles/techFiles/modal.tpl.html',
        controller: 'TechModalController',
        backdrop: 'static',
        controllerAs: 'vm',
        animation: true,
        size: 'xlg',
        resolve: {
          id: function() {
            return angular.extend({
              deptCode: vm.selectedDept
            }, data);
          }
        }
      });

      modalInstance.result.finally(function() {
        load();
      });
    }

    function load(deptCode) {
      vm.expanedIndex = null;
      vm.expanded = false;
      if (deptCode) {
        vm.selectedDept = deptCode;
      } else {
        deptCode = vm.selectedDept;
      }
      techFilesService.getNurseList(deptCode).then(function(rsp) {
        vm.data = rsp.data.data.lst;
      });
    }

    function loadDetail(userCode) {
      techFilesService.getNurseHirReg(userCode).then(function(rsp) {
        vm.detail.nurseHirReg = rsp.data.data;
      });
      techFilesService.getNurseExp(userCode).then(function(rsp) {
        vm.detail.nurseExp = rsp.data.data;
      });
      techFilesService.getAcademic(userCode).then(function(rsp) {
        vm.detail.academic = rsp.data.data;
      });
      techFilesService.getAwardPunish(userCode).then(function(rsp) {
        vm.detail.awardPunish = rsp.data.data;
      });
      techFilesService.getPerAssessment(userCode).then(function(rsp) {
        vm.detail.perAssessment = rsp.data.data;
      });
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('TechModalController', TechModalController);

  /** @ngInject */
  function TechModalController($rootScope, $scope, $uibModalInstance, $filter, NgTableParams, deptService, techFilesService, id) {
    var vm = this,
      nurse = id,
      nurseId = nurse.userCode,
      capitalize = $filter('capitalize'),
      filter = $filter('filter'),
      loaders = {
        getNurseHirReg: function() {
          techFilesService.getNurseHirReg(nurseId).then(function(rsp) {
            vm.detail.nurseHirReg = rsp.data.data;
          });
        },
        getNurseExp: function() {
          techFilesService.getNurseExp(nurseId).then(function(rsp) {
            vm.detail.nurseExp = rsp.data.data;
          });
        },
        getAcademic: function() {
          techFilesService.getAcademic(nurseId).then(function(rsp) {
            vm.detail.academic = rsp.data.data;
          });
        },
        getAwardPunish: function() {
          techFilesService.getAwardPunish(nurseId).then(function(rsp) {
            vm.detail.award = filter(rsp.data.data, { apType: 1 });
            vm.detail.punish = filter(rsp.data.data, { apType: 2 });
          });
        },
        getPerAssessment: function() {
          techFilesService.getPerAssessment(nurseId).then(function(rsp) {
            vm.detail.perAssessment = rsp.data.data;
          });
        }
      },
      initializer = {
        nurseHirReg: {},
        nurseExp: {
          rank: '一般专科护士'
        },
        academic: {},
        award: { apType: 1 },
        punish: { apType: 2 },
        perAssessment: {}
      };

    deptService.getDeptData().then(function(data) {
      vm.deptData = data.belongedDepts;
    });

    vm.detail = {
      info: nurse
    };
    vm.rankArray = ['一般专科护士', '高级专科护士', '临床护理专家'];
    vm.save = save;
    vm.cancel = cancel;
    vm.addItem = addItem;
    vm.saveItem = saveItem;
    vm.removeItem = removeItem;
    loadDetail();

    function loadDetail() {
      loaders.getNurseHirReg();
      loaders.getNurseExp();
      loaders.getAcademic();
      loaders.getAwardPunish();
      loaders.getPerAssessment();
    }

    function save() {
      var data = _.pick(vm.detail.info, 'deptCode', 'hisCode', 'userName', 'birthday', 'gender', 'graduationDate', 'graduationSchool', 'education');
      techFilesService.updateNurseInfo(data).then(function() {
        $rootScope.$broadcast('toast', {
          type: 'success',
          content: '保存成功'
        });
      });
    }

    function cancel() {
      $uibModalInstance.dismiss();
    }

    function addItem(type) {
      vm.detail[type].push(angular.copy(initializer[type]));
    }

    function saveItem(type, data) {
      var handler = techFilesService['save' + capitalize(type)],
        loader = loaders['get' + capitalize(type)],
        data = angular.extend({}, data);
      purify(data);
      handler(data).then(function() {
        $rootScope.$broadcast('toast', {
          type: 'success',
          content: '保存成功'
        });
        loader();
      });
    }

    function removeItem(type, seqId, i) {
      if (seqId) {
        var handler = techFilesService['delete' + capitalize(type)],
          loader = loaders['get' + capitalize(type)];
        handler(seqId).then(function() {
          $rootScope.$broadcast('toast', {
            type: 'success',
            content: '删除成功'
          });
          loader();
        });
      } else {
        vm.detail[type].splice(i, 1);
      }
    }

    function purify(data) {
      for (var i in data) {
        if (i.indexOf('IsOpen') > -1) {
          delete data[i];
        }
      }
      data.userCode = nurse.userCode;
      data.userName = nurse.userName;
      return data;
    }

  }

})();

