(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('SatNurseTempController', SatNurseTempController);

  /** @ngInject */
  function SatNurseTempController($rootScope, $scope, $uibModal, NgTableParams, satNurseService) {
    var vm = this;

    vm.rootScope = $rootScope;
    vm.scope = $scope;

    //删除
    vm.del = del;
    //添加
    vm.open = open;
    // 加载列表
    vm.load = load;

    load();

    function del(id) {
      satNurseService.deleteSatTemp(id).then(function(response) {
        $rootScope.$broadcast('toast', {
          type: 'success',
          content: '删除成功'
        });
        load();
      });
    }

    function open(data) {
      var modalInstance = $uibModal.open({
        templateUrl: 'app/sat/nurse/temp.modal.html',
        controller: 'SatNurseTempModalController',
        controllerAs: 'vm',
        backdrop: 'static',
        animation: true,
        size: 'lg',
        resolve: {
          id: function() {
            return data;
          }
        }
      });

      modalInstance.result.finally(function() {
        load();
      });
    }

    function load() {
      satNurseService.getSatTemp().then(function(rsp) {
        vm.lst = rsp.data.data.lst;
      });
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('SatNurseTempModalController', SatNurseTempModalController);

  /** @ngInject */
  function SatNurseTempModalController($rootScope, $scope, $uibModalInstance, NgTableParams, satNurseService, id) {
    var vm = this,
      handler;

    if (id) {
      vm.formData = id;
      handler = satNurseService.updateSatTemp;
    } else {
      vm.formData = {
        satTempDetailList: []
      };
      handler = satNurseService.saveSatTemp;
    }

    vm.rootScope = $rootScope;
    vm.scope = $scope;

    //删除
    vm.del = del;
    //添加
    vm.add = add;

    vm.ok = ok;
    vm.cancel = cancel;

    function del(i) {
      vm.formData.satTempDetailList.splice(i, 1);
    }

    function add() {
      vm.formData.satTempDetailList.push({
        satOptionList: [{
          optionContent: '满意'
        }, {
          optionContent: '基本满意'
        }, {
          optionContent: '不满意'
        }]
      });
    }

    function ok() {
      handler(vm.formData).then(function() {
        $rootScope.$broadcast('toast', {
          type: 'success',
          content: '保存成功'
        });
        $uibModalInstance.close();
      });
    }

    function cancel() {
      $uibModalInstance.dismiss();
    }

  }
})();

