(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('DepartmentManagerStatisticsController', statisticsControllerFunc);

  /** @ngInject */
  function statisticsControllerFunc(deptService, deptStatisticsService, moment,classesService) {
    var vm = this;
    classesService.getClassList().then(function(response){
        if (response) {
            vm.classesList = response.data.data.lst;
        }
    });
    vm.deptList = deptService.deptList;
    vm.picker = {
      monthOpen: false,
      startDateOpen: false,
      endDateOpen: false
    };

    vm.options = {
      month: {
        datepickerMode: 'month',
        minMode: 'month'
      }
    };

    vm.openPicker = openPicker;
    vm.getStatistics = getStatistics;

    if (vm.deptList.length === 0) {
      deptService.getDeptData().then(function(data) {
        // debugger;
        vm.deptList = data.belongedDepts;
        vm.deptList.unshift({
          deptCode: data.deptCode,
          deptName: data.deptName
        })
      }).then(init)
    }

    function init() {
      vm.queryParams = {
        type: 'leave',
        month: new Date(),
        dept: vm.deptList[0],
        startDate: new Date(),
        endDate: new Date()
      };
      vm.options.startDate = {
        maxDate: vm.queryParams.endDate
      };
      vm.options.endDate = {
        minDate: vm.queryParams.startDate
      }

      getStatistics();
    }

    /**
     * 日期开关
     * @param  {string} name 日期控件的标示
     */
    function openPicker(name) {
      vm.picker[name + 'Open'] = !vm.picker[name + 'Open'];
    }

    /**
     * 获取统计信息
     * @param  {string} type 统计的类型
     */
    function getStatistics(type) {
      var type_temp = type === '' || !type ? 'Leave' : type.replace(/(\w{1})/g, function(match) {
        return match.toUpperCase();
      });
      var params = angular.extend({}, vm.queryParams);

      params.deptCode = params.dept.deptCode;
      params.month = moment(params.month).format('YYYY-MM');

      delete params.dept;

      switch (type_temp) {
        case 'Leave':
          delete params.type;
          delete params.month
          params.startDate = moment(params.startDate).format('YYYY-MM-DD');
          params.endDate = moment(params.endDate).format('YYYY-MM-DD');
          break;

        case 'Days':
          delete params.type;

          break;
        default:
          delete params.type;
          break;
      }

      deptStatisticsService['getBy' + type_temp](params).then(function(response) {
        if (response) {
          vm.statisticsData = response.data.data.lst;
        }
      })
    }
  }

})();

