(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('ChangeRoleController', changeRoleControllerFunc);

  function changeRoleControllerFunc($uibModalInstance, $log, $state, $timeout, moment, $window, toastr, $scope, security) {
    var vm = this;
    var currentRole = security.getUserinfo().role;
    var userId = security.getUserinfo().user.seqId;

    vm.currentRole = security.getUserinfo().role;

    vm.cancel = cancel;
    vm.formData = {};
    vm.datepickerOption = {
      dateDisabled: 'disabled',
      formatYear: 'yyyy',
      formatMonth: 'MM',
      formatDay: 'dd',
      initDate: new Date(),
      maxDate: new Date(2020, 5, 22),
      minDate: moment(),
      startingDay: 1
    };

    vm.validationOption = {
      blurTrig: true
    };

    vm.roleBtnStatus = {
      title: '切换',
      state: ''
    };

    security.getUserRoleList({
      userId: userId
    }).then(function(response) {
      vm.userRoleList = response.data.data;
    });

    vm.selectedRole = currentRole;

    vm.showRole = function(roleObj) {
      $log.info(roleObj);
      vm.changedRole = roleObj;
    };

    vm.changeRole = function() {
      currentRole = security.getUserinfo().role;

      if (!vm.changedRole) return;

      vm.roleBtnStatus.title = '正在切换...';
      vm.roleBtnStatus.state = 'disabled';

      // 检查切换角色是否为当前角色
      if (vm.changedRole.seqId === currentRole.seqId) {
        toastr.info('您切换的角色已是当前登录的角色，无需切换～', '提示');

        resetState();
        return;
      }

      security.getNavigation(vm.changedRole).then(function(response) {
        var userinfo = security.getUserinfo();
        userinfo.nav = response.data.data;
        userinfo.role = vm.changedRole;
        userinfo.tab = {};
        security.setCacheUser(userinfo);
        toastr.success('正在切换角色...', '恭喜');
        $state.go('home');
        $timeout(function() {
          $window.location.reload();
        }, 2000);
      }).finally(resetState);

    }

    function cancel() {
      $uibModalInstance.dismiss('cancel');
    }

    function resetState() {
      vm.roleBtnStatus.title = '切换';
      vm.roleBtnStatus.state = '';
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('acmeBread', acmeBreadFunc);

  /** @ngInject */
  function acmeBreadFunc($http, $log) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/breadcrumb/breadcrumb.html',
      scope: {},
      replace: true,
      controller: breadController,
      controllerAs: 'bc',
      link: loadingLinkFunc,
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function breadController($scope, $interval, moment, $window, $log, $location, $state, security) {
      var bc = this;
      bc.scope = $scope;

      bc.isActive = $scope.isActive;

      bc.tabTemp = security.currentUser.tab;

      /*
      关闭标签页
       */
      bc.removeTab = function($event) {
        // $log.info($event.currentTarget);
        var id = angular.element($event.currentTarget).data('id');
        var prev_li = angular.element($event.currentTarget).parents('li').prev('li');
        var next_li = angular.element($event.currentTarget).parents('li').next('li');
        var state_id;

        // 关闭标签时，如果为当前标签，则路由跳转至前一标签，前一标签没有则跳转至后一标签，只有一个标签页时则跳转至首页；否则不处理跳转。
        if (prev_li.length > 0) {
          state_id = '.' + prev_li.find('a').data('id');
        } else {
          if (next_li.length > 0) {
            state_id = '.' + next_li.find('a').data('id');
          } else {
            // 跳转至首页
            state_id = '';
          }
        }

        // 判别是否为当前标签
        if (angular.element($event.currentTarget).parents('.tab-bread__item').hasClass('active')) {
          $state.go('home' + state_id);
        }

        security.removeFromBreadcrumb(id);
      };


      angular.element(document).ready(function() {
        var tabItem = angular.element('.tab-bread li');
        var tabItemLen = tabItem.length;
        bc.tabItemLen = tabItemLen;
      });

      /*
      高亮显示标签页
       */
      bc.isActive = function(path) {
        var pathLen = path.length;

        return path === $location.path().substr(-pathLen, pathLen);
      };

      $log.info('tab:', bc.tab);

      // "vm.creation" is avaible by directive option "bindToController: true"
    }

    function loadingLinkFunc(scope, elem, attrs) {
        scope.tabItemLen = function() {
            var tabItems = elem.find('li');
            var tabItemLen = tabItems.length;

            return tabItemLen;
        };

        scope.isLoading = function() {
          return $http.pendingRequests.length > 0;
        };

        scope.$watchGroup([scope.tabItemLen, scope.isLoading], function (resArr) {
            var tabWrapperWidth = elem.outerWidth();
            // return;
            if (!resArr[1]) {
                var tabItems = elem.find('li');
                var totalWidth = 0;
                var itemWidth = 0;
                if (resArr[0] === 0) return;

                tabItems.each(function(i, item) {
                    var _item = angular.element(item);
                    var _div = _item.find('.tab-bread__item');
                    var _span = _item.find('span');
                    var innerTextWidth = _span.innerWidth();
                    var tabOuterwidth = _item.outerWidth();
                    var divInnerWidth = _div.width();
                    var realWidth = innerTextWidth + (tabOuterwidth - divInnerWidth);

                    totalWidth += realWidth;
                });

                if (totalWidth > tabWrapperWidth) {
                    itemWidth = tabWrapperWidth / resArr[0];

                    tabItems.width(itemWidth);
                } else {
                    tabItems.width('auto');
                }
            }
            /*
            加载完后计算标签的宽度，大于总宽度时，计算每个标签宽度＝总宽度／标签个数
             */
        });
    }
  }

})();

