(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('modalService', modalServiceFunc);

  function modalServiceFunc($uibModal, $uibModalStack) {
    return {
      open: function(options) {
        $uibModal.open({
          templateUrl: options.templateUrl,
          size: options.size,
          backdrop: 'static',
          appendTo: options.appendTo,
          animation: options.animation || true,
          controller: 'ModalController',
          controllerAs: 'vm',
          resolve: {
            initFn: function() {
              return options.initFn;
            },
            ok: function() {
              return options.ok;
            },
            data: function() {
              if (options.data) {
                return options.data;
              }
            },
            methodsObj: function () {
              if (options.methodsObj) {
                return options.methodsObj;
              }
            }
          }
        });
      },
      close: function(reason) {
        $uibModalStack.dismissAll(reason);
      }
    };
  }
})();

(function (){
    'use strict';

    angular
        .module('nqms')
        .controller('ModalController',ModalControllerFunc);

    function ModalControllerFunc($rootScope, $scope, $uibModalInstance, ok, data, methodsObj, initFn){
      var vm = this;

      vm.rootScope = $rootScope;
      vm.scope = $scope;

      // 预设的表单数据
      vm.formData = data && data.formData;
      // 预设的原始数据
      vm.metaData = data && data.metaData;

      // 在弹层中调用的事件栈
      if (angular.isObject(methodsObj)) {
        for (var prop in methodsObj) {
          if (typeof methodsObj[prop] === 'function') {
            vm[prop] = methodsObj[prop];
          }
        }
      } else {
        if (methodsObj) {
          $log.error('methodsObj必须是对象!');
        }
      }

      initFn && initFn(vm);

      vm.ok = function() {
        ok(vm.formData);
      };

      vm.cancel = function() {
        $uibModalInstance.dismiss('cancel');
      };
    }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('loading', loadingFunc);

  /** @ngInject */
  /**
   * [loadingFunc 全局加载动画]
   * @return {[object]} [指令对象]
   */
  function loadingFunc($http) {
    return {
      restrict: 'E',
      templateUrl: 'app/common/loading/loading.tpl.html',
      link: loadingLinkFunc,
      scope: {},
      controller: function () {
        var vm = this;

        vm.windowWidth = window.innerWidth;
        vm.windowHeight = window.innerHeight;
        vm.loadingWidth = window.innerWidth - 200;
      },
      controllerAs: 'vm'
    };

    function loadingLinkFunc(scope, elem) {
      scope.isLoading = function() {
        return $http.pendingRequests.length > 0;
      };

      scope.$watch(scope.isLoading, function(v) {
        if (v) {
          elem[0].setAttribute('style', 'display: block');
        } else {
          elem[0].setAttribute('style', 'display: none');
        }
      });
    }
  }
})();

