(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('chartsService', chartsService);

  function chartsService($log) {
    var pie = {
        title: {
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          orient: 'horizontal',
          bottom: 'bottom'
        },
        series: [{
          type: 'pie',
          radius: '55%',
          center: ['50%', '50%'],
          label: {
            normal: {
              show: true,
              formatter: "{b} : {c} ({d}%)"
            }
          },
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }]
      },
      horBar = {
        grid: {
          top: '2%',
          right: '2%',
          left: '2%',
          bottom: '2%',
          containLabel: true
        },
        xAxis: [{
          type: 'value'
        }],
        yAxis: [{
          type: 'category',
          axisTick: { show: false },
          data: []
        }],
        serie: {
          type: 'bar',
          label: {
            normal: {
              show: true
            }
          },
          data: []
        },
        series: []
      },
      config = {
        pie: pie,
        horBar: horBar
      },
      service = {
        pieInit: pieInit,
        horBarInit: horBarInit
      };
    return service;

    function pieInit(title, data) {
      var pie = config.pie,
        raw = {
          title: _.extend({
            text: title
          }, pie.title),
          legend: _.extend({
            data: _.pluck(data, 'name')
          }, pie.legend),
          series: [_.extend({
            data: data
          }, pie.series[0])]
        };
      return raw;
    }

    function horBarInit(names) {
      var bar = config.horBar,
        raw = angular.copy(bar, {});
      names.forEach(function(v) {
        var item = angular.copy(raw.serie, {});
        item.name = v;
        raw.series.push(item);
      });
      delete raw.serie;
      return raw;
    }
  }

})();

